@import './fonts-and-colors.scss';

// Reset
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

h1 {
    font-weight: 700;
    color: $primary-font-color;
    font-size: 2.5rem;
}

h2 {
    font-weight: 400;
    color: $primary-font-color;
}

h3 {
    font-weight: 400;
    color: $primary-font-color;
}

p {
    font-size: 1.15rem;
    color: $secondary-font-color;
}

a {
    user-select: none;
}

.link-set {
    .icon {
        transition: all 150ms ease-in-out;

        &:hover {
            scale: 1.25;
            color: $green;
        }
    }
}

button {
    display: inline-block;
    padding: 1rem 2rem;
    font-size: 1rem;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    border: none;
    background-color: $greenGlass50;
    color: $primary-font-color;
    cursor: pointer;
    transition: all 150ms ease-in-out;

    &:hover {
        background-color: $green;
        color: white;
    }
}

header {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    z-index: 1000;
}

main {
    margin-top: 90px;
}

@media screen and (max-width: 768px) {
    main {
        margin-top: 96px;
    }
}

footer {
    margin-top: unset;
}

.page {
    display: flex;
    flex-direction: column;
    width: 100%;

    .head-section {
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        min-height: 300px;
        //border: 5px solid blue;

        .box {
            display: flex;
            flex-direction: column;
            width: 80%;
            max-width: 1200px;
            padding: 2rem;
            margin: auto;
            margin-bottom: 9vw;
            //border: 1px solid green;

            h1 {
                margin-bottom: 2rem;
            }

            p {
                max-width: 400px;
                line-height: 1.3;
            }
        }
    }

    .section {
        display: flex;
        flex-direction: column;
        width: 100%;
        //border: 5px solid red;

        .content-container {
            display: flex;
            place-content: center;
            //border: 5px solid blue;
            width: 100%;

            .box {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 80%;
                max-width: 1200px;
                padding: 2rem;
                margin: auto;
                //border: 1px solid green;
            }
        }
    }
}

// Main Pages Styles
@import './kezdolap.css';
@import './szolgaltatasok.css';
@import './arak.css';
@import './magamrol.css';
@import './kapcsolat.css';
@import './galeria.css';

// Secondary Pages Styles
@import './jo_tudni.css';
@import './akciok.css';
@import './gyik.css';
@import './felhasz_felt.css';

// Component styles
@import './navigation.css';

// Break points
@media screen and (max-width: 1200px) {
    .head-section {
        background-position-y: top !important;
    }
}

@media screen and (max-width: 1050px) {}

@media screen and (max-width: 768px) {
    .box {
        padding: 1rem !important;
        width: 100% !important;
    }

    .head-section {
        .box {
            h1 {
                font-size: 2rem;
                margin-bottom: 1rem !important;
            }
        }
    }

}

