$orange: rgb(255, 165, 0);
$navy: rgb(0, 0, 128);
*/
@import './kezdolap.css';
@import './szolgaltatasok.css';
@import './arak.css';
@import './magamrol.css';
@import './kapcsolat.css';
@import './galeria.css';
@import './jo_tudni.css';
