* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
}

h1 {
  font-weight: 700;
  color: rgb(80, 80, 80);
  font-size: 2.5rem;
}

h2 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

h3 {
  font-weight: 400;
  color: rgb(80, 80, 80);
}

p {
  font-size: 1.15rem;
  color: rgb(100, 100, 100);
}

a {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.link-set .icon {
  transition: all 150ms ease-in-out;
}
.link-set .icon:hover {
  scale: 1.25;
  color: rgb(117, 189, 128);
}

button {
  display: inline-block;
  padding: 1rem 2rem;
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
  letter-spacing: 1px;
  border: none;
  background-color: rgb(186, 222, 191);
  color: rgb(80, 80, 80);
  cursor: pointer;
  transition: all 150ms ease-in-out;
}
button:hover {
  background-color: rgb(117, 189, 128);
  color: white;
}

header {
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  z-index: 1000;
}

main {
  margin-top: 90px;
}

@media screen and (max-width: 768px) {
  main {
    margin-top: 96px;
  }
}
footer {
  margin-top: unset;
}

.page {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.page .head-section {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 300px;
}
.page .head-section .box {
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 1200px;
  padding: 2rem;
  margin: auto;
  margin-bottom: 9vw;
}
.page .head-section .box h1 {
  margin-bottom: 2rem;
}
.page .head-section .box p {
  max-width: 400px;
  line-height: 1.3;
}
.page .section {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.page .section .content-container {
  display: flex;
  place-content: center;
  width: 100%;
}
.page .section .content-container .box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  max-width: 1200px;
  padding: 2rem;
  margin: auto;
}

@media screen and (max-width: 1200px) {
  .head-section {
    background-position-y: top !important;
  }
}
@media screen and (max-width: 768px) {
  .box {
    padding: 1rem !important;
    width: 100% !important;
  }
  .head-section .box h1 {
    font-size: 2rem;
    margin-bottom: 1rem !important;
  }
}/*# sourceMappingURL=App.css.map */